import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory, { textFilter } from 'react-bootstrap-table2-filter';
const data = require('./data.json');

const columns = [{
    dataField: 'id',
    text: '#',
    sort: true
  },{
  dataField: 'nom',
  text: 'Nom',
  sort: true
}, {
  dataField: 'url',
  text: 'Access Site'
}, {
  dataField: 'chef',
  text: 'Chef Etoilé',
  sort: true
}, {
    dataField: 'prix',
    text: 'Prix (euros)',
    sort: true
  },{
    dataField: 'etoile',
    text: 'Etoiles',
    sort: true
  }];

const defaultSorted = [{
  dataField: 'name',
  order: 'desc'
}];


export class MyComp extends React.Component {
    render() {
      return (
        <html>
            <head>
                <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.4.0/css/bootstrap.min.css"/>
            </head>
            <body>
                <div class="container">
                        <BootstrapTable
                        bootstrap4
                        keyField="id"
                        data={ data }
                        columns={ columns }
                        defaultSorted={ defaultSorted } 
                    />
                </div>
            </body>
    </html> 
      )
    }
  }
  
  export default MyComp;
